.container {
  max-width: 1200px;
  margin: 0 auto;

  // padding: 0px 152px 0px 152px;
  padding: 0px 32px 0px 32px;
}

@include onTablet {
  .container {
    padding: 0px 32px 0px 32px;
  }
}

@include onLowTablet {
  .container {
    padding: 0 24px;
  }
}

@include onPhone {
  .container {
    padding: 0 24px;
  }
}
